body {
  margin: 0;
}
@font-face {
  font-family: "OpenSans";
  src: local(opensans), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "OpenSans-Bold";
  src: local(opensans-bold), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "OpenSans-Light";
  src: local(opensans-light), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-weight: light;
  font-style: normal;
  font-stretch: normal;
}
.header-intro__main-title {
  font-family: "OpenSans-Bold", "Arial", sans-serif;
}
.header,
.about,
.processing,
.schedule,
.adresses,
.faq,
.test-task,
.footer,
.test-task-page {
  font-family: "OpenSans", "Arial", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  background-color: #0C99A6;
}
.header__wrapper {
  width: 320px;
  margin: 0 auto;
}
.header-intro {
  padding: 20px;
  box-sizing: border-box;
  background: url("../img/header-bg-mobile.png") no-repeat center 140px;
  background-size: 98%;
  color: white;
}
.header-intro__title {
  height: 210px;
}
.header-intro__title h2 {
  margin: 0;
  padding: 0;
  font-size: 32px;
  letter-spacing: 3px;
}
.header-intro__title p {
  margin: 0;
  padding: 0;
  line-height: 1;
}
.header-intro__main-title {
  margin: 30px 0 20px 0;
  font-size: 34px;
  line-height: 1.2;
}
.header-intro__ask p {
  margin: 0;
}
.header-intro__ask p:nth-child(2) {
  font-weight: bold;
  color: #FF7800;
}
.header-intro__description {
  font-weight: bold;
}
.header-intro__flash {
  padding: 0 5px;
  background-color: #00626B;
}
.toggle {
  display: block;
  width: 100%;
  margin: 40px 0;
  padding: 10px;
  box-sizing: border-box;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  color: white;
  background-color: #4EB543;
  border: 2px solid #000;
  border-radius: 10px;
  cursor: pointer;
}
.toggle:hover {
  background-color: #FF7800;
}
.toggle:active {
  opacity: 0.7;
}
.main-nav {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #00626B;
}
.main-nav__wrapper {
  position: relative;
  width: 320px;
  margin: 0 auto;
}
.main-nav__toggle {
  position: absolute;
  right: 15px;
  width: 60px;
  height: 60px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 4px;
  transform: translateX(-50%);
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 -10px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #FF7800;
  box-shadow: 0 10px 0 0 #FF7800, 0 -10px 0 0 #FF7800;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.5;
}
.main-nav--opened .main-nav__toggle::after,
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 35px;
  right: 10px;
  width: 34px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:hover::before {
  background-color: #FF7800;
}
.main-nav--opened .main-nav__toggle:active::after,
.main-nav--opened .main-nav__toggle:active::before {
  opacity: 0.5;
}
.site-logo {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-logo__link img {
  padding: 5px;
}
.site-logo__link img:hover {
  outline: 1px solid #FF7800;
}
.site-logo__link img:active {
  opacity: 0.5;
}
.main-nav--closed .site-list {
  display: none;
}
.site-list {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__item {
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.site-list__item:first-child {
  border-top: 1px solid #000000;
}
.site-list__link {
  display: block;
  padding: 25px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
}
.site-list__link:hover {
  background-color: #FF7800;
}
.site-list__link:active {
  opacity: 0.3;
}
.about {
  width: 320px;
  margin: 0 auto;
}
.about__title {
  margin: 50px 0 30px 0;
  padding: 0 20px;
  font-size: 18px;
  text-align: center;
  color: #0C99A6;
}
.about__list {
  padding: 0 40px 0 40px;
  font-weight: bold;
  color: #0C99A6;
  list-style: none;
}
.about__item {
  position: relative;
  height: 30px;
}
.about__item::before {
  content: "";
  position: absolute;
  top: 6px;
  left: -20px;
  width: 10px;
  height: 10px;
  background-color: #0C99A6;
  border-radius: 50%;
}
.about__important p {
  margin-top: 50px;
  padding: 0 20px;
  font-size: 18px;
  text-align: center;
  font-weight: bold;
  color: #FF7800;
}
.about__important p:nth-child(2) {
  margin-top: 0;
  font-size: 16px;
}
.processing-wrapper {
  background-color: #0C99A6;
}
.processing {
  position: relative;
  width: 320px;
  margin: 0 auto;
  padding: 20px;
  padding-bottom: 15px;
  box-sizing: border-box;
  text-align: center;
  color: white;
}
.processing h2 {
  font-size: 18px;
}
.processing p {
  margin: 40px 20px 10px 20px;
  font-weight: bold;
}
.processing__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 30px;
  text-align: left;
}
.processing__list::before,
.processing__list::after {
  content: "";
  position: absolute;
  top: 70px;
  left: 20px;
  width: 20px;
  height: 480px;
  background-color: #00626B;
  border-radius: 10px;
}
.processing__list::after {
  top: 218px;
  background-color: #FF7800;
}
.processing__item {
  position: relative;
  padding-bottom: 5px;
  height: 40px;
}
.processing__item a {
  color: white;
  font-weight: bold;
  background-color: #FF7800;
}
.processing__item a:hover {
  background-color: #4EB543;
}
.processing__item a:active {
  opacity: 0.5;
}
.processing__item::before {
  content: "";
  position: absolute;
  top: 6px;
  z-index: 2;
  left: -27px;
  width: 14px;
  height: 14px;
  background-color: white;
  border-radius: 50%;
}
.processing__item:nth-child(2),
.processing__item:nth-child(6),
.processing__item:nth-child(7),
.processing__item:nth-child(9),
.processing__item:nth-child(12) {
  border-bottom: 1px solid #FF7800;
}
.processing__item:nth-child(2)::after,
.processing__item:nth-child(6)::after,
.processing__item:nth-child(7)::after,
.processing__item:nth-child(9)::after,
.processing__item:nth-child(12)::after {
  content: "0 день";
  position: absolute;
  bottom: 0;
  right: 0;
  color: #FF7800;
  font-size: 12px;
}
.processing__item:nth-child(6)::after {
  content: "1 неделя";
  font-weight: normal;
}
.processing__item:nth-child(7)::after {
  content: "1 месяц";
}
.processing__item:nth-child(9)::after {
  content: "2 месяц";
}
.processing__item:nth-child(12)::after {
  content: "3 месяц";
}
.processing__item:nth-child(3) {
  padding-top: 10px;
  font-size: 12px;
  line-height: 1;
  font-weight: bold;
  color: #5b5b5b;
}
.processing__item:nth-child(3)::before {
  width: 0;
}
.processing__item:nth-child(5),
.processing__item:nth-child(6) {
  height: 10px;
}
.processing__item:nth-child(6) {
  padding-top: 10px;
  padding-bottom: 20px;
  font-size: 12px;
  line-height: 1;
  font-weight: bold;
  color: #5b5b5b;
}
.processing__item:nth-child(6)::before {
  width: 0;
}
.processing__item:nth-child(7) {
  height: 30px;
  margin-top: 15px;
}
.processing__item:nth-child(8),
.processing__item:nth-child(10),
.processing__item:nth-child(13) {
  margin-top: 15px;
}
.processing__item:nth-child(9),
.processing__item:nth-child(12) {
  height: 30px;
}
.advantages {
  background-color: #0C99A6;
}
.advantages__wrapper {
  width: 280px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #00626B;
}
.advantages h2 {
  margin-top: 0;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.advantages__item {
  margin-bottom: 20px;
  background: url('../img/advantages-1.png') no-repeat center 0;
  border: 1px #00626B solid;
}
.advantages__item:last-child {
  margin-bottom: 0;
}
.advantages__item:nth-child(1) {
  background-image: url('../img/advantages-1.png');
}
.advantages__item:nth-child(2) {
  background-image: url('../img/advantages-2.png');
}
.advantages__item:nth-child(3) {
  background-image: url('../img/advantages-1.png');
}
.advantages__title {
  margin-top: 50px;
  font-size: 18px;
  font-weight: bold;
}
.for-you {
  background-color: #0C99A6;
}
.for-you__wrapper {
  width: 280px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  background-color: #00626B;
}
.for-you__wrapper h2 {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  font-size: 18px;
}
.for-you__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.for-you__item {
  margin: 15px 0;
  position: relative;
  padding-left: 20px;
  text-align: left;
}
.for-you__item::before {
  position: absolute;
  left: -10px;
  content: "1";
  width: 20px;
  height: 20px;
  font-size: 16px;
  text-align: center;
  border-radius: 50%;
  background-color: #FF7800;
}
.for-you__item:nth-child(2)::before {
  content: "2";
}
.for-you__item:nth-child(3)::before {
  content: "3";
}
.for-you__link {
  font-weight: bold;
  color: #fff;
  background-color: #FF7800;
}
.for-you__link:hover {
  background-color: #4EB543;
}
.for-you__link:active {
  opacity: 0.5;
}
.for-you__wrapper img {
  width: 100%;
}
.schedule-wrapper {
  background-color: #e5e5e5;
}
.schedule,
.adresses {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 10px;
  text-align: center;
}
.schedule ul,
.adresses ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.schedule h2,
.adresses h2 {
  margin: 0;
  padding-top: 30px;
  color: #FF7800;
}
.adresses h2 {
  padding-top: 10px;
  color: #0C99A6;
}
.schedule__item,
.adresses__item {
  position: relative;
  width: 280px;
  margin: 15px auto;
  border: 1px solid #FF7800;
  border-radius: 5px;
}
.adresses__item {
  border: 1px solid #0C99A6;
}
.schedule__day,
.adresses__metro {
  position: absolute;
  top: 22px;
  left: 10px;
  margin: 0;
  padding: 0;
  font-size: 12px;
}
.schedule__floor,
.adresses__floor {
  font-size: 18px;
  font-weight: bold;
  color: #FF7800;
}
.adresses__floor {
  color: #0C99A6;
}
.schedule__content,
.adresses__content {
  display: none;
}
.schedule__content img {
  width: 95%;
}
.schedule__item input,
.adresses__item input {
  display: none;
}
.schedule__label::after,
.adresses__label::after {
  content: "\002B";
  position: absolute;
  top: 15px;
  right: 10px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  font-size: 32px;
  font-weight: 700;
  color: #FF7800;
  transition: all 0.2s linear;
}
.adresses__label::after {
  color: #0C99A6;
}
input:checked ~ .schedule__label::after,
input:checked ~ .adresses__label::after {
  transform: rotate(45deg);
}
input:checked ~ .schedule__content,
input:checked ~ .adresses__content {
  display: block;
}
.schedule__map p,
.adresses__map p {
  width: 80%;
  margin: 0 auto;
  line-height: 1.3;
  font-size: 14px;
  color: grey;
}
.schedule-group,
.schedule-person {
  margin: 20px;
  padding: 10px 20px;
  background-color: #fff;
}
.schedule-group h3,
.schedule-person h3 {
  margin-top: 0;
  line-height: 1;
}
.schedule-group .schedule-group__list,
.schedule-person .schedule-person__list {
  margin: 15px;
}
.price {
  width: 320px;
  margin: 0 auto;
}
.price__title {
  margin: 50px 0 30px 0;
  padding: 0 20px;
  font-size: 24px;
  text-align: center;
  color: #FF7800;
}
.price__list {
  margin: 0;
  padding: 0;
  padding: 0 0 0 20px;
  font-weight: bold;
  color: #0C99A6;
  list-style: none;
}
.price__item {
  position: relative;
  margin: 25px;
  font-size: 16px;
}
.price__item:nth-child(2),
.price__item:nth-child(3) {
  color: grey;
  opacity: 0.5;
}
.price__item span {
  margin: 0;
  padding: 6px;
  border: 1px solid lightgrey;
}
.price__item p {
  margin: 5px;
  padding: 0;
  font-size: 14px;
  font-weight: normal;
  color: red;
}
.price__item::before {
  content: "";
  position: absolute;
  top: 6px;
  left: -20px;
  width: 10px;
  height: 10px;
  background-color: #0C99A6;
  border-radius: 50%;
}
.price__item:nth-child(2)::before,
.price__item:nth-child(3)::before {
  background-color: grey;
}
.faq-wrapper {
  background-color: #FF7800;
}
.faq {
  width: 320px;
  margin: 0 auto;
  text-align: left;
  color: #fff;
}
.faq h2 {
  margin: 0;
  padding: 20px 0;
  text-align: center;
}
.faq__list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-bottom: 5px;
}
.faq__item {
  min-height: 60px;
  margin: 15px 5px;
  border: 1px solid black;
  border-radius: 5px;
}
.faq__answer {
  font-size: 14px;
  space-lettering: 2;
  color: #5b5b5b;
}
.faq__label {
  padding-top: 15px;
  font-size: 18px;
  color: #fff;
}
.faq input:checked + .faq__label {
  color: #5b5b5b;
}
.test-task-wrapper {
  background-color: #0C99A6;
}
.test-task {
  width: 320px;
  min-height: 310px;
  margin: 0 auto;
  text-align: left;
  color: #fff;
  background: url("../img/bg-footer-mobile.png") no-repeat center 95%;
}
.test-task h2 {
  margin: 0;
  padding: 20px 0 0 20%;
  line-height: 1.3;
  font-size: 22px;
}
.test-task p {
  margin: 0;
  margin-bottom: 130px;
  padding: 0 0 0 20%;
  line-height: 1.3;
  font-size: 22px;
  font-weight: bold;
}
.test-task__toggle {
  width: 90%;
  margin: 20px auto;
}
.test-task-page-wrapper {
  background-color: #e5e5e5;
}
.test-task-page__wrapper {
  border: 1px solid #0C99A6;
}
.test-task-page {
  width: 320px;
  margin: 0 auto;
  padding: 15px;
  box-sizing: border-box;
  text-align: center;
}
.test-task-page h1 {
  margin: 0;
  padding: 20px;
}
.test-task-page p {
  margin: 0;
  padding: 15px;
  font-size: 16px;
}
.test-task-page__list {
  width: 280px;
  margin: 10px auto;
  padding: 5px;
  box-sizing: border-box;
  text-align: left;
}
.test-task-page__item,
.test-task-page__luck {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  margin: 15px;
  padding-left: 10px;
  line-height: 1.2;
  font-size: 14px;
  font-style: italic;
}
.test-task-page__item::before {
  position: absolute;
  content: "";
  top: 2px;
  left: -10px;
  width: 10px;
  height: 10px;
  background-color: #FF7800;
  border-radius: 50%;
}
.footer-nav-wrapper {
  background-color: #fff;
}
.footer-nav {
  width: 320px;
  margin: 0 auto;
}
.footer-nav h2 {
  margin-top: 30px;
  margin-bottom: 10px;
  text-align: center;
  font-size: 18px;
  line-height: 1.2;
}
.footer-nav p {
  width: 65%;
  margin: 0 auto;
  padding: 0;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.2;
}
.footer-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 20px;
}
.footer-nav__item {
  margin: 10px;
}
.footer-nav__link {
  font-weight: bold;
  color: #00626B;
}
.footer-nav__link:hover {
  background-color: #FF7800;
}
.footer-nav__link:active {
  opacity: 0.5;
}
.copyright-wrapper {
  background-color: #00626B;
}
.copyright {
  width: 280px;
  margin: 0 auto;
  text-align: center;
  color: #fff;
}
.copyright h3 {
  padding-top: 15px;
  font-size: 16px;
}
.copyright__contacts p {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-size: 14px;
}
.copyright__me {
  padding-bottom: 15px;
  font-size: 12px;
}
.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 55%;
  margin: 20px auto;
}
.social__item {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.social__link {
  width: inherit;
  height: inherit;
}
.social__img {
  width: inherit;
  height: inherit;
  fill: #fff;
}
.social__img:hover {
  fill: #FF7800;
}
.social__img:active {
  opacity: 0.5;
}
.tabs input {
  display: none;
}
.tab {
  overflow: hidden;
}
.tab-label {
  display: flex;
  justify-content: space-between;
  padding: 12px;
  line-height: 24px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s linear;
}
.tab-label:after {
  content: "\002B";
  width: 24px;
  height: 24px;
  font-size: 32px;
  text-align: center;
  transition: all 0.2s linear;
}
.tab-content {
  display: none;
  max-height: 0;
}
input:checked + .tab-label {
  color: #000;
}
input:checked + .tab-label:after {
  transform: rotate(45deg);
}
input:checked ~ .tab-content {
  display: block;
  max-height: 1000px;
  padding: 0 16px 16px;
}
